.overview-page {
  .icons-container {
    display: flex;
    flex-shrink: 0;
    .icon {
      width: 33%;
      text-align: center;
      padding: 0 40px;
      font-weight: 300;
      font-size: 16px;
      line-height: 25px;
    }
  }
  h2 {
    color: #804A80;
    margin: 10px 0;
    text-align: center;
  }
  .organization-row {
    margin: 60px 0;
    .organization {
      text-align: center;
      width: 25%;
      display: inline-block;
    }
  }
}



